/**************Modal Styles***************/
.modal_container,
.modal_container2,
.modal_container3 {
  z-index: 100000000000000000000;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
}

.modal_container.open,
.modal_container2.open,
.modal_container3.open {
  opacity: 1;
  visibility: visible;
}

.modal_body {
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #fff;
  border-radius: 10px;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
}

