/**************Modal Styles***************/
.modal_container,
.modal_container2,
.modal_container3 {
  z-index: 100000000000000000000;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
}

.modal_container.open,
.modal_container2.open,
.modal_container3.open {
  opacity: 1;
  visibility: visible;
}

.modal_body {
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #fff;
  border-radius: 10px;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
}

.half {
  width: 50vw;
}

.full {
  width: 70vw;
}

.modal_container.open .modal_body,
.modal_container2.open .modal_body,
.modal_container3.open .modal_body {
  opacity: 1;
  visibility: visible;
  top: 50%;
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
}

.close_modal {
  font-size: 1.4em;
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 1em;
  padding: 0.5em;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.scroll_body,
.scroll_body2 {
  float: left;
  width: 100%;
  /*    overflow-x: scroll;*/
  padding: 0 1.5em;
  position: relative;
}

.scroll_body2 {
  padding: 1em 1em 2em 1em;
  max-height: 65vh;
  overflow-y: scroll;
  margin-bottom: 2em;
}
.table-wrapper {
  width: 100%;
  float: left;
}

.fl-table {
  border-radius: 5px;
  font-size: 12px;
  font-weight: normal;
  border: none;
  border-collapse: separate;
  border-spacing: 0 1.5em;
  width: 100%;
  max-width: 100%;
  white-space: nowrap;
}

.fl-table td,
.fl-table th {
  text-align: left;
  padding: 0.8em 1em;
  position: relative;
  background: white;
}

.fl-table tr {
  position: relative;
  border-radius: 50px;
}

.fl-table tbody td {
  cursor: pointer;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  background: white;
}

.fl-table tbody td:nth-child(1) {
  padding-left: 1em;
}

.fl-table tbody tr:hover {
  color: var(--blue);
}

.fl-table tr:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(13, 110, 255, 0.05);
  box-shadow: 0 0 0 1px var(--blue);
  opacity: 0;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0, 1);
  border-radius: 10px;
  z-index: 1000;
  pointer-events: none;
}

.fl-table tbody tr:hover:before {
  opacity: 1;
}
.fl-table th:nth-child(1),
.fl-table td:nth-child(1) {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding-left: 1em;
}

.fl-table th:nth-last-child(1),
.fl-table td:nth-last-child(1) {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.fl-table td {
  border-right: none;
  font-size: 1.1em;
  padding: 1.5em 1em;
}

.fl-table thead th {
  color: #000;
  font-size: 1.1em;
  font-weight: 100;
  font-family: "Proxima Nova Semibold";
}

